// footer
.footer {
	position: relative;
	background-color: #323232;
	color: #fff;

	&__copyright {
		position: relative;
		text-align: center;
		padding: 2em 0;
		color: #fff;
		font-size: 1.4rem;
		border-top: 2px solid #000;
		z-index: 300;
		background-color: #323232;
	}
	&__row {
		display: flex;
		flex-direction: column;
		padding: 2em;
		width: 90%;
		gap: 2em;
		& p {
			font-size: 1.6rem;
			// 2.4r
		}
		&--middle {
			justify-content: center;
			align-items: center;
			background-color: #212121;
		}
	}
	&__box {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		overflow: hidden;
		padding: 4em 0;
		gap: 4em;
		z-index: 3;
		&-square1 {
			bottom: -30px;
			left: -20px;
			background-color: #f0eb77;
			transform: rotate(45deg);
		}
		&-square2 {
			top: -30px;
			right: 0;
			border: 2px solid #000;
			background-color: #6a2e94;
			border-radius: 50%;
			transform: rotate(45deg);
		}
	}
}
